$("._toggle-mob-menu").click(function () {
    $("body").toggleClass("_open-mob-menu")
    $("body").toggleClass("fixed-body")
})


$("._search-field").on("input",function () {
    if($(this).val().length > 3){
        $(this).parents(".m-search").addClass("_show")
    }
})

$(document).mouseup( function(e){
    var div = $( ".tabs-select,.m-search,.tablet-menu,.organization-add__field" );
    if ( !div.is(e.target)
        && div.has(e.target).length === 0 ) {
        if($(div).hasClass("_show")){
            $(div).removeClass("_show")
        }
    }
});

$(".header-search__close").click(function () {
    $(".m-search").removeClass("_show")
})

$(".main-slider").click(function () {
    $(".m-search").removeClass("_show")
})

$(".search-field__btn").click(function () {
    $(this).siblings("input").focus()
})


$(".nav__link").each(function (i,el) {
    if(i > 4){
        $(".tablet-menu__links").append($(el).clone())
        $(el).addClass("_hide-tablet")
    }
})


$(".tablet-menu__show").click(function () {
    $(".tablet-menu").toggleClass("_show")
})

$("._toggle-catalog-menu").click(function () {
    $("body").toggleClass("_open-catalog-menu")
    if(window.innerWidth <= 992 && window.innerWidth > 576){
        $("body").toggleClass("fixed-body")
    }
})

$("._toggle-catalog-menu").mouseover(function () {
    if(window.innerWidth >= 993){
        $("body").addClass("_open-catalog-menu")
    }
})

$(document).mouseup( function(e){
    var div = $( ".catalog-menu" );
    if ( !div.is(e.target)
        && div.has(e.target).length === 0 ) {
        if($("body").hasClass("_open-catalog-menu")){
            if(window.innerWidth >= 993){
                $("body").removeClass("_open-catalog-menu")
            }
        }
    }
});

let menuHasChildrenItems = $(".catalog-categories .menu-item-has-children")

console.log(menuHasChildrenItems)

$(menuHasChildrenItems).each(function (i,el) {
    let title = $(el).children("a").html()
    let submenu = $(el).children("ul")
    let backBtn = `
    <li class="catalog-submenu__back">
        <svg width="12" height="16" viewBox="0 0 12 16" fill="none" xmlns="http://www.w3.org/2000/svg">
            <path d="M3.50503 9.50502C3.23166 9.77839 3.23166 10.2216 3.50503 10.495L7.9598 14.9497C8.23316 15.2231 8.67638 15.2231 8.94975 14.9497C9.22311 14.6764 9.22311 14.2332 8.94975 13.9598L4.98995 10L8.94975 6.0402C9.22312 5.76684 9.22312 5.32362 8.94975 5.05025C8.67638 4.77689 8.23317 4.77689 7.9598 5.05025L3.50503 9.50502ZM5 9.3L4 9.3L4 10.7L5 10.7L5 9.3Z" fill="#363A3A"/>
        </svg>
        <span>${title}</span>
    </li>
    `

    console.log(backBtn)
    $(submenu).prepend(backBtn)
})



$(".menu-item-has-children").mouseover(function () {
    if(window.innerWidth >= 993){
        $(".menu-item-has-children").removeClass("active")
        $(this).addClass("active")
    }
})

$(".menu-item-has-children:not(.active)").click(function () {
    if(window.innerWidth < 993){
        $(".menu-item-has-children").removeClass("active")
        $(this).addClass("active")
        $(".catalog-categories").addClass("_open-submenu")
    }
})

$(document).on('click', '.catalog-submenu__back', function(e) {
    $(".menu-item-has-children").removeClass("active")
    $(".catalog-categories").removeClass("_open-submenu")
})

$("._open-mob-catalog").click(function () {
    $("body").toggleClass("_open-catalog-menu")
})

$(".catalog-categories__title").click(function () {
    if(window.innerWidth <= 577){
        $("body").removeClass("_open-catalog-menu")
    }
})
