setUserTypeFields()
$("input[name='userType']").change(function () {
    setUserTypeFields()
})
$(".pay-field input").change(function () {
    setUserTypeFields()
})

function setUserTypeFields() {
    let checkedVal = $("input[name='userType']:checked").val()

    let payWay = $(`.user-type._${checkedVal} input:checked`).val()


    $("._pay-type-output").html(payWay)
    $(".user-type").removeClass("_active")
    $(`.user-type._${checkedVal}`).addClass("_active")
}

setDeliveryTypeFields()
$("input[name='deliveryType']").change(function () {
    setDeliveryTypeFields()
})
$(".address-field input").change(function () {
    setDeliveryTypeFields()
})

function setDeliveryTypeFields() {
    let checkedField = $("input[name='deliveryType']:checked")
    let checkedVal = $(checkedField).val()
    if(!checkedField){
        let deliveryTitle = $(checkedField).parents(".delivery-type__item").find(".text16").html().trim()
        $("._delivery-way-output").html(`${deliveryTitle}:`)
    }

    let deliveryAddress = $(`.delivery-way._${checkedVal} input:checked`).val()
    $("._delivery-address-output").html(deliveryAddress)


    $(".delivery-way").removeClass("_active")
    $(`.delivery-way._${checkedVal}`).addClass("_active")
}

$("._toggle-auth").click(function () {
    $("body").toggleClass("_open-auth")
    $("body").toggleClass("fixed-body")
})
