$("[data-copy]").click(function () {
    let text = $(this).attr("data-copy")
    let $tmp = $("<textarea>");
    $("body").append($tmp);
    $tmp.val(text).select();
    document.execCommand("copy");
    $tmp.remove();
    $(".copy-alert").remove()

    $(".m-page").append(`
    <div class="info-alert copy-alert _active">
        <div class="info-alert__icon">
            <img src="img/check5.svg" alt="">
        </div>
        <div class="info-alert__title">
            <div class="text15">
                Скопировано
            </div>
        </div>
        <button class="info-alert__close">
            <svg width="10" height="10" viewBox="0 0 10 10" fill="none" xmlns="http://www.w3.org/2000/svg">
                <path opacity="0.5" d="M9.66937 0.226864C9.52925 0.0864247 9.33901 0.0075 9.14062 0.0075C8.94224 0.0075 8.752 0.0864247 8.61188 0.226864L4.94438 3.88686L1.27688 0.219364C1.13675 0.0789247 0.946513 0 0.748125 0C0.549737 0 0.359499 0.0789247 0.219375 0.219364C-0.073125 0.511864 -0.073125 0.984364 0.219375 1.27686L3.88687 4.94436L0.219375 8.61186C-0.073125 8.90436 -0.073125 9.37686 0.219375 9.66936C0.511875 9.96186 0.984375 9.96186 1.27688 9.66936L4.94438 6.00186L8.61188 9.66936C8.90438 9.96186 9.37687 9.96186 9.66937 9.66936C9.96187 9.37686 9.96187 8.90436 9.66937 8.61186L6.00187 4.94436L9.66937 1.27686C9.95438 0.991864 9.95438 0.511864 9.66937 0.226864Z" fill="#4A4A4A"/>
            </svg>
        </button>
    </div>
    `)

    setTimeout(()=>{
        $(".info-alert").addClass("_active")
    },10)

    $(".info-alert__close").click(function () {
        closeAlert(".info-alert")
    })

    setTimeout(()=>{
        closeAlert(".info-alert")
    },10000)
})

$("._show-more-reviews").click(function () {
    $(".catalog-detail").find("[data-tab='_tab3']").click()
})
